.bin-wrapper {
  height: fit-content;
  width: 90%;
  background-color: rgba(0, 0, 0, 0.1);
  margin: 2rem auto 0 auto;
  padding: 0rem;
  display: flex;
  flex-direction: row;
}
#bin1 {
  margin: 1rem auto 0 auto !important;
}
.bin-wrapper:last-child {
  margin: 2rem auto 2rem auto !important;
}

.story-icon {
  width: 35%;
  height: 100%;
  margin: 0;
  position: relative;
  border: none;
  cursor: pointer;
  padding: 1rem 0 1rem 0;
  border-top: 3px solid #cc533a;
  border-bottom: 3px solid #cc533a;
}

.story-image {
  display: block;
  margin: auto 0 auto auto;
  width: 95%;
  height: auto;
  border-radius: 4px;
  border: 3px solid transparent;
}
.story-image:hover {
  border: solid 3px #eecc83;
}

.story-details {
  height: 90%;
  max-width: 60%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: auto 1rem auto 1rem;
  color: #aacedb;
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
}
.story-title {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  color: #85b9cc;
  font-family: "Montserrat", sans-serif;
}
.story-slugline {
  margin-top: 0.25rem;
  margin-bottom: 1rem;
  font-size: 17px;
  font-weight: 500;
  color: #eecc83;
  font-family: "Montserrat", sans-serif;
}
.story-description {
  width: 100%;
  color: #f8f8f8;
}
.story-description div {
  margin-bottom: 1rem;
}
.story-awards {
  flex-direction: column;
  justify-content: flex-end;
  vertical-align: bottom;
  position: relative;
  margin-top: auto;
  margin-bottom: 0.5rem;
  width: 100%;
}

@media (max-width: 800px) {
  .bin-wrapper {
    height: fit-content;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.05);
    border-top: 2px solid #cc533a;
    border-bottom: 2px solid #cc533a;
    margin: 1.5rem auto 0 auto;
    padding: 0;
    padding-top: 0.5rem;
    flex-direction: column;
  }

  .story-icon {
    width: 90%;
    height: auto;
    margin: auto;
    position: relative;
    padding: 0.5rem 0 0.5rem 0;
    border-top: none;
    border-bottom: none;
  }

  .story-image {
    display: block;
    width: 95%;
    height: auto;
    margin: auto;
  }
  .story-image:hover {
    border: solid 3px #eecc83;
  }

  .story-details {
    height: auto;
    width: 90%;
    max-width: none;
    margin: 0.5rem auto auto auto;
  }
  .story-title {
    font-size: 20px;
    text-align: center;
    line-height: 1.1;
    font-family: "Montserrat", sans-serif;
  }
  .story-slugline {
    margin-top: 0.25rem;
    margin-bottom: 1rem;
    font-size: 14px;
    text-align: center;
    color: #eecc83;
  }
}
