.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: #003c5e;
  color: #f8f8f8;
}

#homepage-name-container {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
}

#homepage-name-container .clickbutton {
  background-color: cyan;
}

.home-name-wrapper {
  width: 60%;
  height: 70vh;
  display: flex;
  align-items: center;
  margin: auto;
  text-align: center;
}

.home-name {
  /*background-color: lime;*/
  width: 80%;
  margin: auto auto auto 3rem;
}

.headshot-container {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
}
.headshot {
  width: 130px;
  height: auto;
  border-radius: 50%;
}

.full-name {
  font-size: 54px;
  font-family: "Montserrat", sans-serif;
  color: #f8cf79;
  font-weight: 700;
}

.title {
  font-family: "Open Sans", sans-serif;
  margin-top: 0.75rem;
  font-size: 20px;
}

.social-container {
  margin: 1rem auto 0 auto;
  width: 120px;
  display: flex;
  justify-content: space-between;
}

.social-icon {
  font-size: 1.5rem;
  color: #f9d994;
}
.social-icon:hover {
  color: #cc533a;
}

.recent-title {
  width: 90%;
  color: #f8cf79;
  margin: 2rem auto 0 auto;
  font-size: 30px;
  font-weight: 600;
  font-family: Montserrat, sans-serif;
}

@media (max-width: 500px) {
  .home-name-wrapper {
    width: 90%;
    height: 55vh;
    display: flex;
    align-items: center;
    margin: auto;
    text-align: center;
  }

  .home-name {
    width: 100%;
    margin: auto;
  }

  .full-name {
    font-size: 36px;
  }
  .title {
    font-family: "Montserrat", sans-serif;
    margin: 0.75rem auto 0 auto;
    font-size: 15px;
    width: 90%;
  }
  .social-container {
    margin: 1rem auto 0 auto;
    width: 80px;
    display: flex;
    justify-content: space-between;
  }

  .social-icon {
    font-size: 1.1rem;
    color: #f9d994;
  }

  .recent-title {
    font-size: 20px;
  }

  .headshot-container {
    margin-bottom: 0;
  }
  .headshot {
    width: 90px;
  }
}
